.text-field{
    margin: 24px 0;
}
.text-field label {
    display: block;
    font-size: 24px;
}
.text-field input {
    background-color: #FFF;
    box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.06);
    border-radius: 10px;
    width: 100%;
    border: none;
    font-size: 24px;
    padding: 24px;
    box-sizing: border-box;
}